  width: 90%;
  height: 80%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: clamp(22px, 2vw, 40px);
  overflow: hidden;
}

.myWin .close {
  width: clamp(40px, 4vw, 60px);
  position: absolute;
  border-radius: 100%;
  border: solid 2px #fff;
  background: #f60;
  right: -15px;
  top: -15px;
  cursor: pointer;
}

.myWin .closen {
  padding-bottom: 100%;
  position: relative;
  transform: rotate(45deg);
}

.myWin .closen i {
  width: 50%;
  height: 2px;
  background: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.myWin .closen i:nth-child(2) {
  width: 2px;
  height: 50%;
}

