  width: 100%;
  max-width: 940px;
  margin: 0 auto;
  padding: clamp(24px, 5vw, 80px) 0;
  position: relative;
}

@media (max-width: 940px) {
  #pages .imgn {
    padding: clamp(24px, 5vw, 80px);
  }

  #pages .text .textn {
    padding: clamp(24px, 5vw, 80px) !important;
    min-height: inherit !important;
  }
