  font-size: clamp(40px, 5vw, 80px);
}
</style>
<script>
import getPageTitle from '@/utils/getPageTitle'

export default {
    name: 'pages',
    data() {
        return {
            list: [],
            detail: null,
            open: false
        }
    },
    props: {
        cur: {
            type: Number,
            default: 0 //默认0
        },
    },
    methods: {
        getDetail(data) {
            let fi = data.filter((el) => {
                return el.url == this.$route.params.url
            })
            if (fi.length > 0) {
                this.detail = fi[0]
                document.title = getPageTitle(this.detail.title)
            }else{
                this.$router.replace({ path: '/404' })
            }

        },
        back(){
            this.$router.back()
        }
    },
    filters: {
        toEnglish: (value) => {
            let year, month, day, cMonth
            year = value.split('-')[0]
            month = value.split('-')[1]
            day = value.split('-')[2]
            switch (month) {
                case '01':
                    cMonth = 'January';
                    break;
                case '02':
                    cMonth = 'February';
                    break;
                case '03':
                    cMonth = 'March';
                    break;
                case '04':
                    cMonth = 'April';
                    break;
                case '05':
                    cMonth = 'May';
                    break;
                case '06':
                    cMonth = 'June';
                    break;
                case '07':
                    cMonth = 'July';
                    break;
                case '08':
                    cMonth = 'August';
                    break;
                case '09':
                    cMonth = 'September';
                    break;
                case '10':
                    cMonth = 'October';
                    break;
                case '11':
                    cMonth = 'November';
                    break;
                case '12':
                    cMonth = 'December';
                    break;
            }
            return cMonth + ' ' + day + ',' + year
        },

    },
    created() {
        let sendData = {
            app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
            model_name: 'game',
            perpage: 1000,
            order: ["mtime DESC", "id DESC"]
        }
        if (this.$store.state.doodles.doodles.length === 0) {
            this.$store.dispatch('doodles/getList', sendData).then((rs) => {
                this.list = rs
                this.getDetail(this.list)
            })
        } else {
            this.list = this.$store.state.doodles.doodles
            this.getDetail(this.list)
        }

    }
}
</script>

